export function getInlineTransform(aplotransforms) {

	//if (filterHover !== "add") {
		//console.log("got into this function")
		const type = "--aplo-transform-string";
	//	console.log(aplotransforms);
		const transformString = (aplotransforms.rotate ? `rotate(${aplotransforms.rotate}deg) ` : '') + 
							(aplotransforms.scale ? `scale(${(aplotransforms.scale)/100}) ` : '') +
							(aplotransforms.translateY ? `translateY(${aplotransforms.translateY}px) ` : '') +
							(aplotransforms.translateX ? `translateX(${aplotransforms.translateX}px) ` : '');
						

		return {[type]:transformString}
}

export function getInlineHoverTransform(aplotransforms) {

		const type = "--aplo-transform-hover-string";
		const transformString = (aplotransforms.rotate ? `rotate(${aplotransforms.rotate}deg) ` : '') + 
							(aplotransforms.scale ? `scale(${(aplotransforms.scale)/100}) ` : '') +
							(aplotransforms.translateY ? `translateY(${aplotransforms.translateY}px) ` : '') +
							(aplotransforms.translateX ? `translateX(${aplotransforms.translateX}px) ` : '');
		/* the square brackets make the value of type the key */
		return {[type]:transformString}

}

export function getClasses(className,attributes,save=false) {

	// add aploshow class in the editor
	const aploshow = !save ? 'aploshow' : '';

	const {
		aploeffect ,
		aplotexture ,
		aploshadow,
		aplocoveroverlay,
		aplopatterngroup,
		aplomask,
		backdropFilter,
		hasFilters,
		hasHoverFilters,
		galleryswitch,
		hasTransforms,
		hasHoverTransforms,
		haspullstyles,
		aploanimation,

	} = attributes;

	const classes = classnames(className, 
		aploeffect ? `aploanim ${aploeffect} ${aploshow}` : '',
		aplotexture ? `is-style-texture-${aplotexture}` : '',
		aploshadow ? `is-style-aplo-shadow-${aploshadow}` : '',
		aplocoveroverlay ? `is-style-aplo-coveroverlay` : '',
		aplopatterngroup ? `is-style-aplo-pg-${aplopatterngroup}` : '',
		aplomask ? `is-style-aplo-mask-${aplomask}` : '',
		backdropFilter ? 'aplo-bd-f' : '',
		hasFilters > 0 ? 'aplo-has-filters' : '',
		hasHoverFilters > 0 ? 'aplo-has-hover-filters' : '',
		galleryswitch ? 'aplo-gs' : '',
		hasTransforms > 0 ? 'aplo-has-transforms' : '',
		hasHoverTransforms > 0 ? 'aplo-has-hover-transforms' : '',
		haspullstyles > 0 ? 'aplo-has-pull-styles' : '',
		aploanimation ? `has-animation` : '',
	)

	return classes;

}